// frontend/pages/create-inspection.js
import { useEffect, useState } from 'react';
import axios from 'axios';
import { useRouter } from 'next/router';

//import component
import StandardDropdown from '../components/StandardDropdown'
import Headers from '../src/components/header'


export default function CreateInspection() {
  const router = useRouter();
  const [form, setForm] = useState({
    name: '',
    standardID: '',
    note: '',
    price: '',
    samplingPoint: [],
    samplingDate: '',
    file: null,
  });
  const [selectedStandard, setSelectedStandard] = useState('');
  const samplingPoints = ['Front End', 'Back End', 'Other'];

  const handleChange = (e) => {
    const { name, value, type } = e.target;
    console.log('Input field changed:', { name, value, type });
    setForm((prevForm) => ({
      ...prevForm,
      [name]: type === 'number' ? parseFloat(value) || '' : value,
    }));

  };

  const handleCheckboxChange = (point) => {
    setForm((prevForm) => ({
      ...prevForm,
      samplingPoint: prevForm.samplingPoint.includes(point)
        ? prevForm.samplingPoint.filter((p) => p !== point)
        : [...prevForm.samplingPoint, point],
    }));
  };

  const handleFileChange = (e) => {
    setForm((prevForm) => ({
      ...prevForm,
      file: e.target.files[0],
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('name', form.name);
    formData.append('standardID', selectedStandard);
    formData.append('note', form.note);
    formData.append('price', form.price);
    formData.append('samplingPoint', JSON.stringify(form.samplingPoint));
    formData.append('samplingDate', form.samplingDate);
    if (form.file) {
      formData.append('file', form.file);
    }

    try {
      const response = await axios.post('http://localhost:8000/history', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        },
      });
      console.log('Form submitted: ', response.data)
      router.push(`/result/${response.data.data.inspectionID}`);

    } catch (err) {
      console.error(err)
    }
  };

  return (
    <div className='min-h-screen bg-gray-100 p-6'>
      <Headers />
      <div className="max-w-3xl mx-auto bg-white p-8 rounded-lg shadow-lg">
        <h1 className="text-2xl font-bold mb-6">Create Inspection</h1>
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Name */}
          <label>
            Name*
            <br />
            <input
              type="text"
              name="name"
              value={form.name}
              onChange={handleChange}
              placeholder='Please Holder'
              required
            />
          </label>
          <br />

          {/* Standard */}
          <StandardDropdown setSelectedStandard={setSelectedStandard} />

          <br />

          {/* File Upload */}
          <label>
            Upload File:
            <input
              type="file"
              accept=".json"
              name="file"
              onChange={handleFileChange}
              placeholder='raw1.json'
            />
          </label>
          <br />
          {/* Note */}
          <label>
            Note:
            <input
              type="text"
              name="note"
              value={form.note}
              onChange={handleChange}
            />
          </label>
          <br />

          {/* Price Field */}
          <label>
            Price:
            <input
              type="number"
              name="price"
              value={form.price}
              placeholder='10,000'
              min="0"
              max="100000"
              step="0.01"
              onChange={handleChange}
            />
            <small> (0 - 100,000, up to 2 decimal places)</small>
          </label>
          <br />

          {/* Sampling Point Checkboxes */}
          <fieldset>
            <legend>Sampling Point:</legend>
            {samplingPoints.map((point) => (
              <label key={point}>
                <input
                  type="checkbox"
                  value={point}
                  checked={form.samplingPoint.includes(point)}
                  onChange={() => handleCheckboxChange(point)}
                />
                {point}
              </label>
            ))}
          </fieldset>
          <br />

          {/* Sampling Date/Time */}
          <label>
            Date/Time of Sampling
            <br />
            <input
              type="datetime-local"
              name="samplingDate"
              value={form.samplingDate}
              onChange={handleChange}
            />
          </label>
          <br />
          {/* Submit Button */}
          <button type="submit">Submit</button>
        </form>
      </div>
    </div>
  );
}